<template>
    <div style="width:90%;">
        <div class="mb-6">
            <h2>Rutina Semanal</h2>
        </div>
        <div>
            <v-form >
                <v-row
                v-for="(plan, index) in listadoEntrenamientos"
                no-gutters
                class="mb-2"
                :key="plan.id">
                    <v-btn :style="ancho" @click="push(plan.dia)">    
                        {{ convertirNumeroADias(plan.dia) +' - '+ plan.nombre }}
                    </v-btn>
                    <v-btn color="red" style="width:4%" v-if="condicion" @click="eliminarPlanDiario(plan.id)">
                        <v-icon>mdi-delete</v-icon>
                    </v-btn>
                </v-row>
            </v-form>    
        </div>
        <div class="d-flex justify-end mt-6">
            <v-btn @click="this.$router.back()" >
                <v-icon>mdi-reply</v-icon>
                <span>Volver</span>
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn @click="dialog=true" v-if="condicion">
                <v-icon>mdi-plus</v-icon>
                <span>AÃ±adir Nuevo</span>
            </v-btn>
        </div> 
        <div>
            <v-dialog 
            v-model="dialog">
                <v-card class="pa-4">
                    <v-form class="pa-4">
                        <v-row>
                            <v-autocomplete
                            label="Dia"
                            :items="diasDisponibles()"
                            v-model="nuevoPlanDiario.dia">
                            </v-autocomplete>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Nombre del plan"
                            v-model="nuevoPlanDiario.nombre">
                            </v-text-field>
                        </v-row>
                        <v-row>
                            <v-text-field
                            label="Descripcion del plan"
                            v-model="nuevoPlanDiario.descripcion">
                            </v-text-field>
                        </v-row>
                    </v-form>
                    <div>
                        <v-row class="pa-4">
                            <v-btn @click="cerrarDialog" >
                                <v-icon>mdi-close-box</v-icon>
                                Cerrar
                            </v-btn>
                            <v-spacer></v-spacer>
                            <v-btn @click="accionGuardar" >
                                <v-icon>mdi-content-save-edit</v-icon>
                                Guardar
                            </v-btn>
                        </v-row>
                    </div>
                </v-card>
            </v-dialog>
        </div>
    </div>
</template>

<script>
import utiles from '../utiles/funciones'
export default {
    data() {
        return{
            dialog: false,
            nuevoPlanDiario:{
                id: 0,
                dia: '',
                nombre: '',
                descripcion: '',
                ejerciciosList: []
            },
            ancho: "width:82%"
        }
    },
    methods:{
        accionGuardar(){
            this.$store.commit('guardarPlanDiarioNuevo', {
                idPlanSemanal:parseInt(this.$route.params.idPlanSemanal),
                id:this.nuevoPlanDiario.id,
                dia:this.convertirDiaANumero(this.nuevoPlanDiario.dia),
                nombre:this.nuevoPlanDiario.nombre,
                descripcion:this.nuevoPlanDiario.descripcion,
                ejerciciosList:this.nuevoPlanDiario.ejerciciosList})
            this.cerrarDialog()

        },
        eliminarPlanDiario(idPlanDiario){
            this.$store.commit('eliminarPlanDiario', {
                idPlanSemanal:parseInt(this.$route.params.idPlanSemanal),
                idPlanDiario:idPlanDiario})
        },
        cerrarDialog(){
            this.dialog = false
            this.nuevoPlanDiario.nombre = '',
            this.nuevoPlanDiario.descripcion = '',
            this.nuevoPlanDiario.dia = ''
        },
        convertirNumeroADias(diaNum){
            return utiles.convertirNumeroADias(diaNum)
        },
        convertirDiaANumero(dia){
            return utiles.convertirDiaANumero(dia)
        },
        diasDisponibles(){
            let array = this.$store.getters.diasDisponibles(parseInt(this.$route.params.idPlanSemanal))
            let convertido = array.map(dia => this.convertirNumeroADias(dia))
            return convertido
        },
        push(dia){
            return this.$router.push(`${this.$router.currentRoute._rawValue.path}/${this.convertirNumeroADias(dia)}`)
        },
        usuarioCliente(){
            this.ancho = "width:100%"
            return false
        }
    },
    computed:{
        listadoEntrenamientos(){
            return this.$store.getters.listadoPlanesDiarios(parseInt(this.$route.params.idPlanSemanal))
        },
        condicion(){
            return (this.$store.getters.tipoUsuario == 1 ? 
            true
            :
            this.usuarioCliente()
        )
        }
    }
}

</script>

